<template>
  <div>
    <div style="border-radius: 4px;margin: 20px 20px 0px 20px; background-color:#f3efec;padding: 10px 20px 10px 20px;display: flex;">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/homePage' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/classManagePage' }">教学班管理</el-breadcrumb-item>
        <el-breadcrumb-item>考勤次数信息管理</el-breadcrumb-item>
      </el-breadcrumb></div>

    <div style="margin: 10px 20px 20px 20px;">
      <el-card class="box-card" style="padding: 0px;">
        <div style="float: left;margin-bottom: 15px;">
          <span>{{this.courseData.classNo}}</span>
          <span>{{this.courseData.courseName}}</span>
          <span>{{this.courseData.teacherName}}</span>
          <span>{{this.courseData.teacherId}}</span>
          <el-button  class="el-button-local" @click="addNewRegister" type="success" size="mini">新增考勤信息</el-button>
<!--          <el-button  class="el-button-local" @click="handleClick(scope.row)" type="success" size="mini">保存考勤表</el-button>-->
          <el-button  class="el-button-local" @click="handleClick(scope.row)" type="success" size="mini">导出EXCEL</el-button>
          <!--          <span style="font-weight: bold">课程序号:</span><el-input v-model="input" placeholder="请输入内容" style="width:220px;"></el-input>-->
          <!--          <span style="font-weight: bold">课程名:</span><el-input v-model="input" placeholder="请输入内容" style="width:220px;"></el-input>-->
        </div>
      </el-card>
      <el-card class="box-card" body-style="padding:0px;">
        <div slot="header" style="display: flex;">
          考勤次数信息列表
        </div>
        <componentRegisterTimeManage v-bind="$attrs"></componentRegisterTimeManage>
      </el-card>

    </div>
<!--    <el-pagination-->
<!--      layout="prev, pager, next"-->
<!--      :page-size="7"-->
<!--      :small="true"-->
<!--      style="float: right;margin-right: 20px;"-->
<!--      :total="100">-->
<!--    </el-pagination>-->
  </div>
</template>

<script>
import componentRegisterTimeManage from '../componentRegisterTimeManage/componentRegisterTimeManage'

export default {
  name: 'register-time-manage-factory',
  components: {
    componentRegisterTimeManage
  },
  data () {
    return {
      courseData: {
        classNo: this.$store.state.storeCurrentClassNo,
        courseName: this.$store.state.storeCurrentCourseName,
        teacherName: this.$cookies.get('user_info').user_name,
        teacherId: this.$cookies.get('user_info').user.user_id
      }
    }
  },
  methods: {
    /**
     * 子组件调用父组件的方法
     */
    addNewRegister () {
      this.$parent.addNewRegisterInfoChildPower()
    }
  }
}
</script>

<style scoped>

</style>



