<template>
  <el-table
    :data="registerInfoArray"
    style="width: 100%;" border>
    <el-table-column
      fixed
      label="序号"
      header-align="center"
      align="center"
      width="150"

    >
      <template slot-scope="scope">
        <span style="font-size: 15px;">第{{scope.$index+1}}次考勤</span>
      </template>
    </el-table-column>
    <el-table-column
      fixed
      prop="time"
      align="center"
      label="考勤时间"
      header-align="center"
      min-width="100"
    >
      <template slot-scope="scope">
        <i class="el-icon-time" style="font-size: 15px"></i>
        <span style="margin-left: 5px;font-size: 15px;">{{ scope.row.time }}</span>
      </template>
    </el-table-column>
    <el-table-column
      prop="every_register_sit"
      align="center"
      label="操作"
      header-align="center"
      min-width="110"
    >
      <template slot-scope="scope">
        <el-button class="el-button-local" @click="redirectToStudentRegisterManage(scope.row)" type="success" size="mini">管理本次签到</el-button>
        <el-button class="el-button-local" @click="deleteRegisterInfo(scope.row)" type="danger" size="mini">删除本次签到</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import axModule from 'axios'

export default {
  name: 'componentRegisterTimeManage',
  props: {
    registerInfoArray: Array,
    user_info: Object
  },
  data () {
    return {}
  },
  methods: {
    redirectToStudentRegisterManage (row) {
      this.$store.commit('setStoreCurrentRegisterTime', row.time)
      this.$router.push({path: '/studentRegisterManagePage'})
    },
    deleteRegisterInfo (row) {
      axModule.get(this.GLOBAL.host + '/deleteRecord', {
        params: {
          user_id: this.user_info.user.userId,
          user_password: this.user_info.user.userPassword,
          classNo: this.$store.state.storeCurrentClassNo,
          time: row.time
        }
      }).then(res => {
        console.log(res)
        if (res.status === 200) {
          this.$message({
            message: '删除成功',
            type: 'success'
          })
          this.$router.go(0)// this.getAllRegisterInfo() //  新增一个签到后，会重新请求设置所有签到
        }
      })
    }
  }
}
</script>

<style scoped>

</style>



