<template>
  <div>
  <head-factory :active='2' :user="user_info" :powerMenu="power"></head-factory>
  <register-time-manage-factory :registerInfoArray='current_register_info' :user_info="user_info"></register-time-manage-factory>
    <el-dialog
      title="发布签到码"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
      center
    >
      <div style="text-align: center;font-size: 15px;">
        <span>签到码为: &nbsp;</span>
        <span><el-tag size="medium" style="font-size: 20px">{{registerData}}</el-tag></span>
      </div>

      <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="requestStopRegister">停止签到</el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
import headFactory from '../../components/head/headFactory/headFactory'
import registerTimeManageFactory from '../../components/body/registerTimeManage/registerTimeManageFactory/registerTimeManageFactory'
import axModule from 'axios'

export default {
  name: 'RegisterTimeManagePage',
  created () {
    if (this.$cookies.isKey('user_info')) {
      this.user_info = this.$cookies.get('user_info')
    } else {
      this.$router.push({path: '/login'})
    }
    this.getAllRegisterInfo()
  },
  components: {
    headFactory,
    registerTimeManageFactory
  },
  data () {
    return {
      user_info: {},
      current_register_info: [],
      power: true,
      registerData: '',
      dialogVisible: false
    }
  },
  methods: {
    setCurrentTerm () {
      this.$router.push({path: '/classManagePage'})
    },
    getAllRegisterInfo () {
      axModule.get(this.GLOBAL.host + '/getAbsenceManageCountInfo', {
        params: {
          user_id: this.user_info.user.userId,
          user_password: this.user_info.user.userPassword,
          classNo: this.$store.state.storeCurrentClassNo,
          classYear: this.$store.state.termInfo.clickChildrenTerm,
          className: this.$store.state.storeCurrentClassName
        }
      }).then(res => {
        // console.log(res)
        if (res.status === 200) {
          this.current_register_info = res.data
        }
      })
    },
    addNewRegisterInfoChildPower () {
      this.dialogVisible = true
      axModule.get(this.GLOBAL.host + '/getManageAbsenceInfo', {
        params: {
          user_id: this.user_info.user.userId,
          user_password: this.user_info.user.userPassword,
          classNo: this.$store.state.storeCurrentClassNo,
          classYear: this.$store.state.termInfo.clickChildrenTerm,
          className: this.$store.state.storeCurrentClassName,
          courseName: this.$store.state.storeCurrentCourseName
        }
      }).then(res => {
        // console.log(res)
        if (res.status === 200) {
          this.registerData = res.data
          this.getAllRegisterInfo() //  新增一个签到后，会重新请求设置所有签到
        }
      })
    },
    handleClose (done) {
      this.$confirm('确认停止签到？')
        .then(_ => {
          this.requestStopRegister()
          done()
        })
        .catch(_ => {})
    },
    requestStopRegister () {
      axModule.get(this.GLOBAL.host + '/endAttendance', {
        params: {
          classNo: this.$store.state.storeCurrentClassNo,
          classYear: this.$store.state.termInfo.clickChildrenTerm
        }
      }).then(res => {
        // console.log(res)
        if (res.status === 200) {
          this.dialogVisible = false
        }
      })
    }
  }
}
</script>

<style scoped>

</style>



